
* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
}
body {
  font-family: 'IBM Plex Sans Condensed', sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 1.7;
  color: #777;
  background-image: -webkit-gradient(linear, left top, left bottom, from(#181616), to(#616060));
  background-image: linear-gradient(to bottom, #181616, #616060);
}
.row {
  margin-left: 8%;
}
.col-md-9 {
  padding-left: 4%;
}
#app {
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.navBar {
  background-color: #2b2a2a;
  position: fixed;
  top: 0;
  width: 100%;
  z-index: 100;
  list-style: none;
  text-align: center;
}
.navBar li {
    display: inline-block;
}
.navBar li a {
      display: inline-block;
      color: #f2f2f2;
      text-align: center;
      padding: 14px 16px;
      text-decoration: none;
}
.navBar li a:hover {
        background: #ddd;
        color: black;
}
#about {
  padding: 70px 10px 0 0%;
  font-size: 18px;
  line-height: 30px;
}
#about img {
    width: 170px;
    height: 260px;
    opacity: 0.9;
}
#about .center {
    float: center;
    color: white;
}
#about .center h2 {
      opacity: 0.95;
      font-weight: bold;
      padding-bottom: 10px;
}
#about .center p {
      color: #8f8f8f;
      font-size: 17px;
}
#about .center #mail {
      color: white;
      opacity: 0.7;
      font-size: 18px;
}
#education {
  padding: 70px 0% 0 0;
  font-size: 18px;
  line-height: 30px;
}
#education .text {
    color: white;
}
#education .text h1 {
      opacity: 0.95;
}
#education .text h2 {
      color: #f0f0f0;
}
#education .text ul {
      color: #8f8f8f;
}
#education .text ul li {
        display: inline;
        list-style-type: disc;
}
#education .text li {
      color: #8f8f8f;
      list-style-type: none;
}
#skills {
  padding: 70px 0% 5% 0;
}
#skills .col-md-9 {
    padding-right: 20%;
}
#skills h1 {
    color: white;
}
.text-left {
  text-align: center !important;
}
@-webkit-keyframes moveInLeft {
0% {
    opacity: 0;
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
}
100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0);
}
}
@keyframes moveInLeft {
0% {
    opacity: 0;
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
}
100% {
    opacity: 1;
    -webkit-transform: translate(0);
            transform: translate(0);
}
}
.bar {
  background-color: black !important;
  color: rgba(255, 255, 255, 0.664);
}
.space {
  background-color: grey;
}



















